"use client";
import { useState } from "react";
import Header from "../components/Header";

export default function LogInPage() {
    const[email, setEmail] = useState("");  //usamos el estado para almacenar variables, eventos reactivos
    return (
    <div>
        <Header showLoginButton={false} />
        <div>
        <label
          className="mb-1 block text-xl font-medium text-slate-100">
            Email
        </label>
        <input
        id="email"
        name ="email"
        type="email"
        value={email}
        onChange={(e) => {
            e.preventDefault();
            setEmail(e.target.value)}
        }
        placeholder="tucorreop@dominio.com"
        autoComplete="email"
        required
        className="w-full rounded-xl border border-pink-700 bg-black"
        />
        <p>Tu email es: {email}</p>
      </div>
    </div>
    );
}